import AActionSheet from './ActionSheet.vue';

import Vue from 'vue';
// 
import createApp from '@/utils/createApp'

/**
 * ActionSheet
 * @param {Object}}  
 * @param {Object}  
 */
const ActionSheet = ({
    title = '',
    cancelText = 'cancel',
    dataSource = [],
    onClickAction = () => {}
} = {}) => {
    let vm = createApp(Vue, AActionSheet, {
        props: {
            title,
            dataSource,
            cancelText
        },

        on: {
            'click-action': (index) => {
                onClickAction(index, dataSource[index]);
                vm.closePanel();
            },

            'update:isShow': isShow => {
                vm.isShow = isShow;
            }
        }
    });
    // isShow
    vm.isShow = true;

    // 
    ActionSheet.destory = () => {
        vm.$destory();
    };

    return vm;
};

// ====================  ====================
// : this.$alert
Vue.prototype.$actionSheet = Vue.prototype.$action = ActionSheet;
export default ActionSheet;


